@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500;600;700&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
    line-height: 1;
    background: #000;
		font-family: 'Rajdhani', sans-serif;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

input[type="text"]
{
    font-size: 18px;
		font-family: 'Rajdhani', sans-serif;
}
.Input {
   background: #000;
   width: 100%;
   line-height: 3;
   outline: 0;
   border: 0;
   border-bottom: 1px solid #FFFF99;
   padding-left: 10px;
   color: #FFFF99;
   font-weight: bold;
   font-size: larger;
}

.Input:focus {
   outline: none !important;
}

.InputBox-Wrapper {
   position: relative;
}

.InputBox {
   background: #FFFF33;
   border-left: 2px solid #181818;
   border-bottom: 2px solid #181818;
   width: 100%;
   padding: 8px;
   opacity: 0.9;
   font-size: medium;
   z-index: 9999;
}

.InputBox ul ol:hover {
   border-left: 2px solid #000;
   padding: 10px;
   font-weight: bold;
   font-size: larger;
 }

.InputBox-Cursor {
  cursor: pointer;
  padding-bottom: 8px;
}

.InputBox-Clear {
   position: absolute;
   top: 15px;
   right: 20px;
   color: #FFF;
   padding: 5px;
   font-size: larger;
   cursor: pointer;
}

.InputBox-Clear:hover {
  font-weight: bolder;
}
.ListBox {
   display: grid;
   grid-template-columns: 1fr 1fr;
   margin-top: 50px;
   grid-column-gap: 70px;
   -webkit-column-gap: 70px;
           column-gap: 70px;
   font-size: 18px;
}

.ListBox div {
    padding-bottom: 30px;
}

.ListBox-Link {
    font-weight: 400;
    text-decoration: none;
    color: #FFFF99;
    cursor: pointer;
}

.ListBox-Link:hover {
   font-weight: bold;
   font-size: larger;
   color: #FFF;
}

p {
    color: #FFF;
    padding-bottom: 10px;
}
.InfoBox {
    padding: 5px;
    margin-top: 20px;
    font-weight: 200;
    font-size: larger;
    min-width: 300px;
}

.Info-Starships {
    font-size: small;
    font-weight: 300;
    color: #FFFF99;
    padding-bottom: 15px
}

.Info-Link {
    padding-bottom: 20px;
}

.Info-Img {
    padding-bottom: 20px;
}

.Info-Img img {
    border: 1px solid #FFFF99;
    width: 350px;
    height: 200px;
}

.Info-Link a {
    font-size: x-large;
    text-decoration: none;
    color: #FFFF99;
}

.Info-Starships ol {
   padding-bottom: 8px;
}

p {
   color: #FFF;
   padding-bottom: 10px;
}
.Container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(-webkit-min-content, 500px));
  grid-template-columns: repeat(auto-fit, minmax(min-content, 500px));
  min-height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
}

.Container-Img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 370px;
  height: 400px;
}

.Container > div {
  background: #000;
  padding: 8px;
}

.Container-Info {
  text-align: center;
  font-size: larger;
}

.Container-Info-Text {
  font-weight: 800;
}

.Container-Error {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #990000!important;
  color: white;
  min-width: 350px;
  max-height: 60px;
  z-index: 10;
  padding: 10px;
  font-weight: 200;
}

.Container-Loading {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #FFFF33!important;
  min-width: 350px;
  max-height: 60px;
  z-index: 10;
  padding: 10px;
  font-weight: 200;
}

.Container-Loading > p {
 color: black;
 font-weight: 300;
}

.Container-Link {
  text-decoration: none;
  color: #FFFF99;
  font-size: 17px;
}

/* Media Queries Section  */

@media only screen and (max-width: 480px) { 
  .Container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .Container-Img {
    display: block;
    width: 300px;
    height: 250px;
  }

  .Content {
    margin: 0 auto;
    max-width: 320px;
  }
}
